.wp-block-vitos-dynamicblock {
  display: flex;
  gap: 20px;
  align-items: stretch;
  justify-content: space-between;
  flex-wrap: wrap;

  > div {
    width: calc(50% - 10px);
    background: #f5f5f5;
    border-radius: 30px;
    padding: 20px;
    box-sizing: border-box;

    img {
      max-width: 100%;
      height: auto;
    }

    h2 {
      font-size: 20px;
      font-weight: 700;
    }
  }
}
